// 2. PromotionSection.tsx (возвращаем кнопку в исходное положение)
"use client";

import React, { useState } from 'react';
import Image from "next/image";
import cat from "../../../../public/statics/cat.png";
import AppointmentModal from '../../app/components/AppointmentModal';

const PromotionSection = () => {
    const [isModalOpen, setIsModalOpen] = useState(false);

    return (
        <>
            <section>
                <div className="w-full text-center">
                    <h2 className="text-2xl md:text-3xl font-bold text-red-600 mb-4">АКЦИЯ</h2>
                </div>
                <div className="w-full">
                    {/* Кнопка над картинкой, как было изначально */}
                    <button
                        onClick={() => setIsModalOpen(true)}
                        className="bg-red-600 hover:bg-red-700 text-white font-bold py-4 px-12 rounded-lg transition-colors shadow-lg hover:shadow-xl text-lg mb-4"
                    >
                        ЗАПИСАТЬСЯ
                    </button>
                    <Image src={cat} alt="котик"/>
                </div>
            </section>

            <AppointmentModal
                isOpen={isModalOpen}
                onClose={() => setIsModalOpen(false)}
            />
        </>
    );
};

export default PromotionSection;